import { BackButton, BackIcon, HeaderDiv,
PeriodoTitle, SwitchButton, SwitchDiv, SwitchImg } from "./style";

type header = {
    title: string;
};

export default function PageHeader({title}: header) {
    return (
        <HeaderDiv>
            <BackButton>
                <BackIcon src={require('../../assets/Back.png')}/>
            </BackButton>

            <SwitchDiv>
                <SwitchButton>
                    <SwitchImg src={require('../../assets/VectorSwitchLeft.svg')}/>
                </SwitchButton>

                <PeriodoTitle>{title} Perodo</PeriodoTitle>
                
                <SwitchButton>
                    <SwitchImg src={require('../../assets/VectorSwitchRight.svg')}/>
                </SwitchButton>
            </SwitchDiv>
        </HeaderDiv>
    );
};