import { useEffect } from 'react';
import { PeriodosDiv } from "./style";
import PageHeader from '../../components/PageHeader';

export default function Periodos() {

/*  Muda o ttulo da pgina s na primeira renderizao  */
    useEffect(() => {
        document.title = 'Periodos';
    }, []);

    return (
        <PeriodosDiv>
            <PageHeader
            title='Primeiro'/>
        </PeriodosDiv>
    );
};